import React, { useState,useEffect } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";
import { useParams} from 'react-router-dom';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {
    Card
} from "react-bootstrap";

import { storage, db,logout } from "./../../firebase";
import firebase from 'firebase/compat/app';




export default function Album(){


  const [imageUrl, setImageUrl] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [createdAt, setCreatedAt] = useState(new Date());



 const { id }= useParams();

 useEffect(() => {
   const unmount = db.collection("events")
     .doc(id)
     .onSnapshot((doc) => {
       setImageUrl(doc.data().imageUrl);
       setTitle(doc.data().title);
       setDescription(doc.data().description);
       setCreatedAt(doc.data().createdAt);



     });
     return unmount
 }, []);


  const options = { month: 'long', day: 'numeric' , year: 'numeric'};

  return (
    <>






      <Card className="mt-4 mb-4" bg="light" >

      <Card.Body>
                    <Card.Img    class="rounded-circle" variant="top"   width="150" height="150"   src={imageUrl}  />
          <Card.Title className="mt-4 mb-4" >   <h6>{title} </h6>  </Card.Title>
          <Card.Text >  <p>    {new Date(createdAt.seconds*1000).toLocaleDateString("en-US",options)}
              </p></Card.Text>
                    {description}

                    </Card.Body>
                    </Card>


    </>
  );
}
