import React, { useState, useEffect } from "react";

import { Link } from "react-router-dom";
import ReactPaginate from "react-paginate";



import {
    Container,
    Row,
    Col,
} from "react-bootstrap";
import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';




const ResearcherHome = () => {
  const [events, setEvents] = useState([]);
  const [currentEvent, setCurrentEvent] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");


  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 10;
  const pagesVisited = pageNumber * usersPerPage;

  useEffect(() => {
    retrieveEvents();
  }, []);

  const onChangeSearchTitle = e => {
    const searchTitle = e.target.value;
    setSearchTitle(searchTitle);
  };

  const retrieveEvents = () => {
    const unmount = db.collection("literatures")
    .orderBy('timestamp','desc')
    .onSnapshot((snapshot) => {
      const tutorials = [];
      snapshot.forEach((doc) => {
        tutorials.push(
          {
            id: doc.id,
              ...doc.data(),
          });
      });
      setEvents(tutorials);
    });
    return unmount;
  };

  const refreshList = () => {
    retrieveEvents();
    setCurrentEvent(null);
    setCurrentIndex(-1);
  };

  const setActiveEvent = (event, index) => {
    setCurrentEvent(event);
    setCurrentIndex(index);
  };





  const pageCount = Math.ceil(events.length / usersPerPage);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };



  return (

    <>

    <Container  bg="danger"  className="mt-4" >
                <Row>
                    <Col lg={8}>   <h1 > Philippine Lakes Related Literature</h1>
                        <Row>


                        <ul className="list-group list-group-flush">
                     {events &&

                       events
                       .slice(pagesVisited, pagesVisited + usersPerPage)
                       .map((img, index) => (
                         <li className="list-group-item" key={index}>


                             <a href={img.fileUrl} target="_blank" rel="noreferrer noopener">{img.title} </a>
                         </li>
                       ))}
                   </ul>


                             <ReactPaginate

                            pageCount={pageCount}
                             onPageChange={changePage}
                             breakClassName={'page-item'}
                             breakLinkClassName={'page-link'}
                             containerClassName={'pagination'}
                             pageClassName={'page-item'}
                             pageLinkClassName={'page-link'}
                             previousClassName={'page-item'}
                             previousLinkClassName={'page-link'}
                             nextClassName={'page-item'}
                             nextLinkClassName={'page-link'}
                             activeClassName={'active'}
                             />

        </Row>
    </Col>


    <Col lg={4}>
    </Col>


</Row>
</Container>





    </>


  );
};

export default ResearcherHome;
