import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";

import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';



export default function Upload({ username }) {


  const [classification, setClassification] = useState("");

  const [firstName, setFirstName] = useState("");
  const [middleName, setMiddleName] = useState("");
  const [lastName, setLastName] = useState("");
  const [suffix, setSuffix] = useState("");
  const [designation, setDesignation] = useState(null);
  const [department, setDepartment] = useState(null);
  const [educationI, setEducationI] = useState("");
  const [educationII, setEducationII] = useState("");
  const [educationIII, setEducationIII] = useState("");
  const [others, setOthers] = useState("");
  const [licensure, setLicensure] = useState("");
  const [email, setEmail] = useState("");

  const [researchInterest, setResearchInterest] = useState('');
  const [project, setProject] = useState("");
  const [imagePreview, setImagePreview] = useState('');








  const [image, setImage] = useState("");
  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);




  function handlePreview(image) {
    setImagePreview(URL.createObjectURL(image));
    setImage(image);
  }

  const handleChange = (e) => {
    if (e.target.files[0]) {
      handlePreview(e.target.files[0]);
    }
  };
  console.log('preview:', imagePreview);


  function handleUpload() {
    const uploadTask = storage.ref(`reseachersImages/${image.name}`).put(image);
    console.log("image:", image);
    console.log("image name:", image.name);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        //progress function
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        //error function
        console.log(error);
        alert(error.message);
      },
      () => {
        //complete function
        storage
          .ref("reseachersImages")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            //post image inside db
            console.log("url:" + url);
            db.collection("researchers").add({

              classification: classification,
              firstName: firstName,
              middleName: middleName,
              lastName: lastName,
              suffix: suffix,
              designation: designation,
              department: department,
              educationI: educationI,
              educationII: educationII,
              educationIII: educationIII,
              others: others,
              licensure: licensure,
              email: email,

              researchInterest: researchInterest,
              project: project,






              imageUrl: url,
              timestamp: firebase.firestore.FieldValue.serverTimestamp(),

            });
          });



          setClassification("");
          setFirstName("");
          setMiddleName("");
          setLastName("");
          setSuffix("");
          setDesignation("");
          setDepartment("");
          setEducationI("");
          setEducationII("");
          setEducationIII("");
          setOthers("");
          setLicensure("");
          setEmail("");
          setResearchInterest("");
          setProject("");





        setProgress(0);
        setImage(null);
        setImagePreview('');
      }
    );
  }

  return (
    <>


      <div >
              <h1>Upload Researcher </h1>






        <select onChange={(e) => setClassification(e.target.value)}>
        <option name="classification" id="classification" value="">Select Classification</option>
       <option name="classification" id="classification" value="Program Leader">Program Leader</option>
       <option name="classification" id="classification" value="Project Leader">Project Leader</option>
       <option name="classification" id="classification" value="Faculty Researcher">Faculty Researcher</option>
       <option name="classification" id="classification" value="Researcher"> Researcher</option>
       </select>
       <br></br>
        <Input
          type="text"
          placeholder="Enter a first name"
          value={firstName}
          onChange={(e) => setFirstName(e.target.value.toUpperCase())}
          className="input"
          color="primary"
        />
        <br></br>
        <Input
          type="text"
          placeholder="Enter a middle initial"
          value={middleName}
          onChange={(e) => setMiddleName(e.target.value.toUpperCase())}
            className="input"
          color="primary"
        />
         <br></br>
        <Input
          type="text"
          placeholder="Enter a last name"
          value={lastName}
          onChange={(e) => setLastName (e.target.value.toUpperCase())}
            className="input"
          color="primary"
        />


             <br></br>
        <select onChange={(e) => setSuffix(e.target.value)}>
       <option name="classification" id="classification" value="">Select Suffix</option>
       <option name="classification" id="classification" value="M.Sc.">Masters of Science</option>
       <option name="classification" id="classification" value="M.A.">Masters of Arts  </option>
       <option name="classification" id="classification" value="M.A.T.">Masters of Arts in Technology </option>
       <option name="classification" id="classification" value="M.I.T.">Masters in Technology </option>
       <option name="classification" id="classification" value="Ph.D.">Doctor in Philosophy</option>
       </select>
     <br></br>
        <Input
          type="text"
          placeholder="Enter a designation"
          value={designation}
          onChange={(e) => setDesignation(e.target.value)}
            className="input"
          color="primary"
        />
     <br></br>


        <select onChange={(e) => setDepartment(e.target.value)}>
        <option name="classification" id="classification" value="">Select College Department</option>
       <option name="classification" id="classification" value="College of Fisheries.">College of Fisheries</option>
       <option name="classification" id="classification" value="College of Food Nutrition and Dietetics">College of Food Nutrition and Dietetics</option>
       <option name="classification" id="classification" value="College of Computer Studies">College of Computer Studies</option>
       <option name="classification" id="classification" value="College of Education">College of Education</option>
       </select>

     <br></br>
        <Input
          type="text"
          placeholder="enter a education B.Sc."
          value={educationI}
          onChange={(e) => setEducationI(e.target.value)}
            className="input"
          color="primary"
        />
     <br></br>
        <Input
          type="text"
          placeholder="Enter a education M.Sc."
          value={educationII}
          onChange={(e) => setEducationII(e.target.value)}
              className="input"
          color="primary"
        />
     <br></br>
        <Input
          type="text"
          placeholder="Enter a education Ph.D."
          value={educationIII}
          onChange={(e) => setEducationIII(e.target.value)}
            className="input"
          color="primary"
        />
     <br></br>
        <Input
          type="text"
          placeholder="Enter others"
          value={others}
          onChange={(e) => setOthers(e.target.value)}
            className="input"
          color="primary"
        />
             <br></br>
        <Input
          type="text"
          placeholder="Enter a license"
          value={licensure}
          onChange={(e) => setLicensure(e.target.value)}
            className="input"
          color="primary"
        />
             <br></br>
        <Input
          type="text"
          placeholder="Enter email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
            className="input"
          color="primary"
        />

     <br></br>
        <TextareaAutosize
          maxRows={4}
          aria-label="minimum height"
          minRows={10}
          placeholder="Enter Research Interest "
          style={{ width: 450 }}
          value={researchInterest}
          onChange={(e) => setResearchInterest(e.target.value)}
            className="input"
          />

     <br></br>
        <Input
          type="text"
          placeholder="Enter a project"
          value={project}
          onChange={(e) => setProject(e.target.value)}
            className="input"
          color="primary"
        />

     <br></br>
        <input
          type="file"
          onChange={handleChange}
          className="input"
          style={{ display: "none" }}
          id="raised-button-file"
        />
        <label htmlFor="raised-button-file">
          <IconButton
            variant="raised"
            component="span"
            className="button"
            color="primary"
          >
          <img
        src={imagePreview||"https://www.brno.cz/o/brno-theme/assets/images/placeholders/avatar/avatar-unisex.png?v=0.1.0"}
        alt=''
          className="image"
        width="150"
        height="150"
      />
            <PhotoCamera />
          </IconButton>
        </label>
        <LinearProgress
          variant="determinate"
          color="primary"
          value={progress}
            className="progress"
        />
        <Button variant="outlined" onClick={handleUpload}>
          Upload
        </Button>
      </div>
    </>
  );
}
