import React, { useState, useEffect } from "react";

import { Link } from "react-router-dom";
import {
    Container,Card,Col,Row
} from "react-bootstrap";

import ReactPaginate from "react-paginate";
import ResearcherPost from "./allresearcherpost";
import { storage, db } from "./../firebase";
import firebase from 'firebase/compat/app';


const EventsList = () => {
  const [events, setEvents] = useState([]);
  const [currentEvent, setCurrentEvent] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");


  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 6;
  const pagesVisited = pageNumber * usersPerPage;


  useEffect(() => {
    retrieveEvents();
  }, []);

  const onChangeSearchTitle = e => {
    const searchTitle = e.target.value;
    setSearchTitle(searchTitle);
  };

  const retrieveEvents = () => {
    const unmount = db.collection("researchers")
    .orderBy('timestamp','desc')
    .onSnapshot((snapshot) => {
      const tutorials = [];
      snapshot.forEach((doc) => {
        tutorials.push(
          {
            id: doc.id,
              ...doc.data(),
          });
      });
      setEvents(tutorials);
    });
    return unmount;
  };

  const refreshList = () => {
    retrieveEvents();
    setCurrentEvent(null);
    setCurrentIndex(-1);
  };

  const setActiveEvent = (event, index) => {
    setCurrentEvent(event);
    setCurrentIndex(index);
  };



  const pageCount = Math.ceil(events.length / usersPerPage);
  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };




  return (
    <div>


      <Container   bg="danger"  className="mt-4" > <h3> Researcher</h3>


      <Col lg={12} className="mt-4 mb-4" md='3' align='center' >
      <Row >
        {events
          .slice(pagesVisited, pagesVisited + usersPerPage)


          .map((event) => (
              <Col lg={2}    onClick={() => setActiveEvent(event)}  >

              <ResearcherPost
                id={event.id}

                firstName={event.firstName}
                middleName={event.middleName}
                lastName={event.lastName}

                    lastName={event.lastName}
                          suffix={event.suffix}
                imageUrl={event.imageUrl}

              />


              </Col>
          ))}
       </Row>
       </Col>

       <div className="mt-4">
     <ReactPaginate
        previouslabel={"Prev"}
        nextLabel={"Next"}
        pageCount={pageCount}
        onPageChange={changePage}
        containerClassName={"paginationBttns"}
        previousLinkClassName={"previousBttn"}
        nextLinkClassName={"nextBttn"}
        disabledClassName={"paginationDisabled"}
        activeClassName={"paginationActive"}
      />
      </div>
      </Container>




        {currentEvent ? (

         <Card className='mt-4' bg="light" >
          <Container className='mt-4 mb-4' >
         <Row>

         <Col   md= '3'>

           <Card.Img  class="rounded-circle" variant="top" src={currentEvent.imageUrl} height="300" width="250"/>

         </Col>

              <Col md='6'>

                      <Card.Title><h2>  {currentEvent.classification}</h2> </Card.Title>
                      <Card.Body >
                              <h3>{currentEvent.firstName} {currentEvent.middleName} {currentEvent.lastName} {currentEvent.suffix} </h3>
                                  {currentEvent.designation}  ,  {currentEvent.department}   <br></br>
                                <Row className='mt-4' >
                                  <Col >
                                  <h5>Education Background </h5>

                                    {currentEvent.educationI}  <br></br>
                                    {currentEvent.educationII} <br></br>
                                    {currentEvent.educationIII} <br></br>


                                    {currentEvent.licensure}  <br></br>
                                    {currentEvent.others}

                                     <br></br><br></br>

                                      {currentEvent.email}  
                                  </Col>
                                  <Col >
                                    <h5>Research Interest </h5>
                                    {currentEvent.researchInterest}    <br></br>




                                  </Col>
                                </Row>
                      </Card.Body>


                </Col>
         </Row>
           </Container >
         </Card>

        ) : (
          <>

          </>
        )}




    </div>
  );
};

export default EventsList;
