import React, { useState, useEffect } from "react";
import {Link} from 'react-router-dom'


import {
    Card,
     Container,
      Row,
      Col,

} from "react-bootstrap";

export default function Homepost({album,name,images}){


  return(


  <div>





                      <Card  md='2' className="mt-4" bg="light" >

                          <Card.Body>
                          <Card.Img       variant="top"   width="250" height="250"    src={images ? images[0].url : ""} alt="album"  />



                              <Card.Title >  <h6> {name}</h6>  </Card.Title>
                              <Card.Text >  <p>
                                            </p>


                              </Card.Text>


                          </Card.Body>
                      </Card>












  </div>

)
}
