import  React, { useState , useEffect } from 'react'
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
export const DateTime = () => {

    var [date,setDate] = useState(new Date());

    useEffect(() => {
        var timer = setInterval(()=>setDate(new Date()), 1000 )
        return function cleanup() {
            clearInterval(timer)
        }

    });
const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    return(
    <div>
          <Typography component="div">
          <Box sx={{  fontWeight: '1000',  fontSize: 10 , fontStyle: 'italic' , color:"white" ,mt:5, lineHeight: 3 }}>  Philippine Standard Time</Box>
          <Box sx={{ fontWeight: '1000',  fontSize: 13 , fontStyle: 'italic' , color:"white" , lineHeight: 1}}>   {new Date(date).toLocaleDateString("en-US",options)}</Box>
          <Box sx={{ fontWeight: '2000',  fontSize: 20 , fontStyle: 'italic' , color:"white"  }} > {date.toLocaleTimeString()}</Box>
        </Typography>


        </div>
    )
}

export default DateTime
