import React from "react";

import { List, ListItem, ListItemIcon, ListItemText } from '@mui/material';


import { Link } from "react-router-dom";

export default ({ children }) => {
  return (
    <>
      <List>
        <Link to="/login">
          <ListItem button>
            <ListItemIcon>
        
            </ListItemIcon>
            <ListItemText primary="Login" />
          </ListItem>
        </Link>
        <Link to="/register">
          <ListItem button>
            <ListItemIcon>

            </ListItemIcon>
            <ListItemText primary="Register" />
          </ListItem>
        </Link>
      </List>
    </>
  );
};
