import React, { useState, useEffect } from "react";
import { useParams,useNavigate , Link } from "react-router-dom";

import  AlbumPost  from "./albumpost";
import {
    Card,
     Container,
      Row,
      Col,

} from "react-bootstrap";
import ReactPaginate from "react-paginate";

import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';


export default function Album(){
  const [images, setImages] = useState([]);
  const [albumName, setAlbumName] = useState("");
  const [description, setDescription] = useState("");



  let navigate = useNavigate("/album");
  const { id }= useParams();

  useEffect(() => {
    const unmount = db.collection("albums")
      .doc(id)
      .onSnapshot((doc) => {
        setImages(doc.data().images || []);
        setAlbumName(doc.data().name);
        setDescription(doc.data().description);
      });
      return unmount
  }, [id]);


  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 12;
  const pagesVisited = pageNumber * usersPerPage;

  const pageCount = Math.ceil(images.length / usersPerPage);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };

  return (
    <>


                                  <Card className='mt-4 mb-4' >
                                  <p align='right'>Go back to <Link to="/album">Album List page</Link></p>

                                    <Container >
                                    <Row  >
                                      <Card.Title><h2 align='center' > {albumName}</h2> </Card.Title>
                                        <Card.Body >
                                        <p align='center'>    {description}</p>
                                        </Card.Body>
                                    </Row>
                                  </Container>
                                  </Card>

    <Container  bg="danger"  className="mt-4" align="center" >
    <Row>
        {images
         .slice(pagesVisited, pagesVisited + usersPerPage)
          .map((image) => (
          <Col lg={4}>
          <AlbumPost
            albums={image.albums}
            albumsName={image.name}
            url={image.url}
          />
            </Col>
        ))}



        <div className="mt-4">
        <ReactPaginate
        pageCount={pageCount}
        onPageChange={changePage}
        breakClassName={'page-item'}
        breakLinkClassName={'page-link'}
        containerClassName={'pagination'}
        pageClassName={'page-item'}
        pageLinkClassName={'page-link'}
        previousClassName={'page-item'}
        previousLinkClassName={'page-link'}
        nextClassName={'page-item'}
        nextLinkClassName={'page-link'}
        activeClassName={'active'}
        />
       </div>

      </Row>
</Container>



    </>
  );
};
