import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";

import { db } from "./../../firebase";
import firebase from 'firebase/compat/app';
import {
    Card,
     Container,
      Row,
      Col,

} from "react-bootstrap";


export default function NewAlbumForm() {
  const [albumName, setAlbumName] = useState("");
  const [description, setDescription] = useState("");



  const onAlbumCreate = () => {

    db.collection("albums")
    .doc()
    .set({
      name: albumName,
      timestamp:  firebase.firestore.FieldValue.serverTimestamp(),
      description: description,
      
        });

    setAlbumName("");
    setDescription("");
  };

  return (
    <>




                                                <Card className='mt-4 mb-4'  lg={12}>
                                                  <Container >
                                                  <Row  >
                                                    <Col lg={4}>
                                                        <Card.Title><h2 align='center' >{albumName}</h2> </Card.Title>
                                                      <Card.Body >
                                                      <TextareaAutosize
                                                        maxRows={2}
                                                        aria-label="minimum height"
                                                        minRows={10}
                                                        placeholder="Enter Album Name "
                                                        style={{ width: 450 }}
                                                        value={albumName}
                                                        onChange={(e) => setAlbumName(e.target.value)}

                                                        />
                                                        <TextareaAutosize
                                                          maxRows={4}
                                                          aria-label="minimum height"
                                                          minRows={10}
                                                          placeholder="Enter Description "
                                                          style={{ width: 450 }}
                                                          value={description}
                                                          onChange={(e) => setDescription(e.target.value)}

                                                          />

                                                              <button onClick={onAlbumCreate}>Create album</button>
                                                      </Card.Body>




                                                        </Col>
                                                          </Row>
                                                        </Container>
                                                </Card>
    </>
  );
};
