import React, { useState, useEffect } from "react";
import { Link   } from "react-router-dom";


import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';


export default function Home() {
    const [programs, setPrograms] = useState([]);
/*
  const [imageUrl, setImageUrl] = useState();
  const [title, setTitle] = useState();
    const [description, setDescription] = useState();
      const [timestamp, setTimestamp] = useState();


useEffect(() => {
const unmount = db.collection("boards")
  .doc()
  .onSnapshot((doc) => {
    setImageUrl(doc.data().imageUrl);
    setTitle(doc.data().title);
    setDescription(doc.data().description);
    setTimestamp(doc.data().timestamp);
  });
  return unmount
}, []);
*/
useEffect(() => {
  const unmount = db.collection("events")
  .orderBy('createdAt','desc')
  .onSnapshot((snapshot) => {
    const tutorials = [];
    snapshot.forEach((doc) => {
      tutorials.push(
        {
          id: doc.id,
            ...doc.data(),
        });
    });
    setPrograms(tutorials);
  });
  return unmount;
}, []);






  function handleDelete(id) {
   if(window.confirm("Delete the item?")){
    db.collection('boards')
      .doc(id)
      .delete()
      .then(() => {
        console.log('Document successfully deleted!');
      })
      .catch((error) => {
        console.error('Error removing document: ', error);
      });
    }else {
      console.error('Error removing document: ');
    }
  }


const options = { month: 'long', day: 'numeric' , year: 'numeric'};

    return (
      <div className="container">
        <div className="panel panel-default">
          <div className="panel-heading">
            <h3 className="panel-title">BOARD LIST</h3>
          </div>
          <div className="panel-body">
            <h4>
              <Link to="/useradd">Add Board</Link>
            </h4>
            <table className="table table-stripe">
              <thead>
                <tr>
                  <th>Title</th>
                  <th>Description</th>
                  <th>date</th>
                  <th>image</th>
                  <th>status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {programs.map(album => (
                  <tr id={album.id}>
                    <td>  <Link to={`/user/${album.id}`}>{album.title}</Link> </td>
                    <td> {album.description}</td>
                    <td> {new Date(album.createdAt.seconds*1000).toLocaleDateString("en-US",options)} </td>
                    <td>  <img     variant="top"   width="50" height="50"   src={album.imageUrl}  />  </td>
                    <td> {album.published}</td>
                    <td>
                      <button
                        onClick={() => handleDelete(album.id)}

                        className="btn btn-danger"
                      >
                        Delete
                      </button>


                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
