import React, { useState, useEffect } from "react";

import { Link } from "react-router-dom";
import {
    Container,Card,Col,Row
} from "react-bootstrap";

import ReactPaginate from "react-paginate";
import ResearcherPost from "./leaderpost";
import { storage, db } from "./../firebase";
import firebase from 'firebase/compat/app';


const EventsList = () => {
  const [events, setEvents] = useState([]);
  const [currentEvent, setCurrentEvent] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");


  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 6;
  const pagesVisited = pageNumber * usersPerPage;


  useEffect(() => {
    retrieveEvents();
  }, []);

  const onChangeSearchTitle = e => {
    const searchTitle = e.target.value;
    setSearchTitle(searchTitle);
  };

  const retrieveEvents = () => {
    const unmount = db.collection("researchers")
    .where('classification' , '==', 'Program Leader')
    .orderBy('timestamp','desc')
    .onSnapshot((snapshot) => {
      const tutorials = [];
      snapshot.forEach((doc) => {
        tutorials.push(
          {
            id: doc.id,
              ...doc.data(),
          });
      });
      setEvents(tutorials);
    });
    return unmount;
  };

  const refreshList = () => {
    retrieveEvents();
    setCurrentEvent(null);
    setCurrentIndex(-1);
  };

  const setActiveEvent = (event, index) => {
    setCurrentEvent(event);
    setCurrentIndex(index);
  };




  const pageCount = Math.ceil(events.length / usersPerPage);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };




  return (
    <div>


      <Container   bg="danger"  className="mt-4" > 
      <Col lg={12} className="mt-4 mb-4" md='3' align='left' >
      <Row >
        {events
          .slice(pagesVisited, pagesVisited + usersPerPage)
          .map((event) => (
              <Col lg={12}    onClick={() => setActiveEvent(event)}  >
              <ResearcherPost
                id={event.id}
                imageUrl={event.imageUrl}
                firstName={event.firstName}
                middleName={event.middleName}
                lastName={event.lastName}
                lastName={event.lastName}
                suffix={event.suffix}
                designation={event.designation}
                department={event.department}
                educationI={event.educationI}
                educationII={event.educationII}
                educationIII={event.educationIII}
                licensure={event.licensure}
                others={event.others}
                email={event.email}
                researchInterest={event.researchInterest}
              />
              </Col>
          ))}
       </Row>
       </Col>
       {/*}
       <div className="mt-4">
     <ReactPaginate
        previouslabel={"Prev"}
        nextLabel={"Next"}
        pageCount={pageCount}
        onPageChange={changePage}
        containerClassName={"paginationBttns"}
        previousLinkClassName={"previousBttn"}
        nextLinkClassName={"nextBttn"}
        disabledClassName={"paginationDisabled"}
        activeClassName={"paginationActive"}
      />
      </div>

      */}
        </Container>






    </div>
  );
};

export default EventsList;
