

import React, { useEffect, useState } from "react";
import {useNavigate} from "react-router-dom"
import {
    Card
} from "react-bootstrap";
import { Button } from "@mui/material";

import {paragraph} from "./styles.css";


export default function EventPost({id, firstName,middleName, lastName, suffix, designation, imageUrl }) {


  return (


<>

          <Card className="mt-4 mb-4" bg="light" >

              <Card.Body>
                            <Card.Img    class="rounded-circle" variant="top"   width="150" height="150"   src={imageUrl}  />
                  <Card.Title className="mt-4 mb-4" >   <h6>{firstName}  {middleName}  {lastName} , {suffix}</h6>  </Card.Title>
                  <Card.Text >  <p>{designation}</p></Card.Text>
              
              </Card.Body>
          </Card>
</>





  );
}
