import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';

const EventView = props => {
  const { id }= useParams();
  let navigate = useNavigate();

  const initialEventState = {
    id: null,
    title: "",
    description: "",
    published: false
  };
  const [currentEvent, setCurrentEvent] = useState(initialEventState);
  const [message, setMessage] = useState("");

  const [imageUrl, setImageUrl] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [createdAt, setCreatedAt] = useState(new Date());


  useEffect(() => {
    const unmount = db.collection("events")
      .doc(id)
      .onSnapshot((doc) => {
        setImageUrl(doc.data().imageUrl);
        setTitle(doc.data().title);
        setDescription(doc.data().description);
        setCreatedAt(doc.data().createdAt);

      });
      return unmount
  }, [id]);

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentEvent({ ...currentEvent, [name]: value });
  };
/*
  const updatePublished = status => {
    var data = {
      id: currentEvent.id,
      title: currentEvent.title,
      description: currentEvent.description,
      published: status
    };

    EventDataService.update(currentEvent.id, data)
      .then(response => {
        setCurrentEvent({ ...currentEvent, published: status });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  const updateEvent = () => {
    EventDataService.update(currentEvent.id, currentEvent)
      .then(response => {
        console.log(response.data);
        setMessage("The Event was updated successfully!");
      })
      .catch(e => {
        console.log(e);
      });
  };

  const deleteEvent = () => {
    EventDataService.remove(currentEvent.id)
      .then(response => {
        console.log(response.data);
        navigate("/events");
      })
      .catch(e => {
        console.log(e);
      });
  };
*/
  return (
    <div>
      {currentEvent ? (
        <div className="edit-form">
          <h4>Event</h4>
          <form>
            <div className="form-group">
              <label htmlFor="title">Title</label>
              <input
                type="text"
                className="form-control"
                id="title"
                name="title"
                value={currentEvent.title}
                onChange={handleInputChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="description">Description</label>
              <input
                type="text"
                className="form-control"
                id="description"
                name="description"
                value={currentEvent.description}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>
                <strong>Status:</strong>
              </label>
              {currentEvent.published ? "Published" : "Pending"}
            </div>
          </form>
    {/*
          {currentEvent.published ? (

            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(false)}
            >
              UnPublish
            </button>
          ) : (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(true)}
            >
              Publish
            </button>
          )}

          <button className="badge badge-danger mr-2" onClick={deleteEvent}>
            Delete
          </button>

          <button
            type="submit"
            className="badge badge-success"
            onClick={updateEvent}
          >
            Update
          </button>
          */}
          <p>{message}</p>
        </div>
      ) : (
        <div>
          <br />
          <p>Please click on a Event...</p>
        </div>
      )}
    </div>
  );
};

export default EventView;
