import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";


import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


import { storage, db,logout } from "./../../firebase";
import firebase from 'firebase/compat/app';



export default function Upload({ username }) {



  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [published, setPublished] = useState(true);
  const [category, setCategory] = useState("");
 const [createdAt, setCreatedAt] = useState(new Date());




  const [imagePreview, setImagePreview] = useState('');
  const [image, setImage] = useState("");
  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);




  function handlePreview(image) {
    setImagePreview(URL.createObjectURL(image));
    setImage(image);
  }

  const handleChange = (e) => {
    if (e.target.files[0]) {
      handlePreview(e.target.files[0]);
    }
  };
  console.log('preview:', imagePreview);


  function handleUpload() {
    const uploadTask = storage.ref(`videofiles/${image.name}`).put(image);
    console.log("image:", image);
    console.log("image name:", image.name);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        //progress function
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        //error function
        console.log(error);
        alert(error.message);
      },
      () => {
        //complete function
        storage
          .ref("videofiles")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            //post image inside db
            console.log("url:" + url);
            db.collection("videofiles")
            .add({

              title: title,
              description: description,
              published: true,
              category: category,
              createdAt: createdAt,





              imageUrl: url,


            });
          });



          setTitle("");
          setDescription("");
          setPublished(true);
          setCategory("");
          setCreatedAt("");




        setProgress(0);
        setImage(null);
        setImagePreview('');
      }
    );
  }

  return (
    <>



      <div >
          <h1>Upload Video file </h1>



          <select onChange={(e) => setCategory(e.target.value)}>
          <option name="classification" id="category" value="">Choose Category</option>
         <option name="classification" id="category" value="image">image</option>
         <option name="classification" id="category" value="Others">Others</option>
         </select>
         <br></br>
        <TextareaAutosize
          maxRows={4}
          aria-label="minimum height"
          minRows={10}
          placeholder="enter title "
          style={{ width: 450 }}
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="input"
          />
           <br></br>

        <TextareaAutosize
          maxRows={4}
          aria-label="minimum height"
          minRows={10}
          placeholder="enter description "
          style={{ width: 450 }}
          value={description}
          onChange={(e) => setDescription(e.target.value)}
              className="input"
          />





            <DatePicker
            selected={createdAt}
            onChange={date => setCreatedAt(date)}
            />


        <input
          type="file"
          onChange={handleChange}

          style={{ display: "none" }}
          id="raised-button-file"
              className="input"
        />
        <label htmlFor="raised-button-file">
          <IconButton
            variant="raised"
            component="span"

            color="primary"
                className="button"
          >
          <img
        src={imagePreview||"https://thealmanian.com/wp-content/uploads/2019/01/product_image_thumbnail_placeholder.png"}
        alt=''
          className="image"
        width="150"
        height="150"
      />
            <PhotoCamera />
          </IconButton>
        </label>
        <LinearProgress
          variant="determinate"
          color="primary"
          value={progress}
            className="progress"
        />
        <Button variant="outlined" onClick={handleUpload}>
          Upload
        </Button>
      </div>
    </>
  );
}
