import React from "react";
//import "./Posts.css";
import {
    Container,
    Row,
    Col,

    Form,
    Button,
    FloatingLabel
} from "react-bootstrap";




const ContactUs = () => {

  return (



    <Container>

    <Row  className="mt-4"> <h1  className="mb-3"> Contact Us</h1>
        <Col lg={8}>
            <Row>


                          <Form>
  <Form.Group className="mb-1" controlId="formBasicEmail">
    <Form.Label></Form.Label>
    <Form.Control type="name" placeholder="Name" />
    <Form.Text className="text-muted" >

    </Form.Text>
    </Form.Group>

  <Form.Group className="mb-4" controlId="formBasicPassword">
    <Form.Label> </Form.Label>
    <Form.Control type="email" placeholder="Enter email" />
  </Form.Group>



  <FloatingLabel className="mb-3"   controlId="floatingTextarea2" label="Message">
    <Form.Control
      as="textarea"
      placeholder="Leave a comment here"
      style={{ height: '100px' }}
    />
  </FloatingLabel>


  <Button variant="primary" type="submit" className="mb-1" >
    Send Message
  </Button>

</Form>

                        </Row>




                        <Row className="mt-4">
  <Col sm>
        <h4> Address</h4>
        <p>Malinta, Los Baños, Laguna</p>


  </Col>

  <Col sm>
    <h4> Contact</h4>
    <p>Tel.no.: </p>
    <p>Email Adress: nicerprojectII@gmail.com </p>

    </Col>


  <Col sm>
  <h4> Hours</h4>
    <p>Mon-Fri: 8:00 - 5:00 pm </p>
    <p>Sat-Sun: Closed </p>
    </Col>
</Row>


                    </Col>


                    <Col lg={4}>

                    </Col>


                </Row>
            </Container>
  );
};

export default ContactUs;
