import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";



import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';



export default function Upload({ username }) {

  const [category, setCategory] = useState("");

  const [programTitle, setProgramTitle] = useState("");
  const [projectTitle, setProjectTitle] = useState("");


  const [projectLeader, setProjectLeader] = useState("");
  const [agency, setAgency] = useState("");
  const [address, setAddress] = useState("");
  const [telephone, setTelephone] = useState("");
  const [mobile, setMobile] = useState("");
  const [fax, setFax] = useState("");
  const [email, setEmail] = useState("");


  const [cooperatingAgency, setCooperatingAgency] = useState("");
  const [createdAt, setCreatedAt] = useState(new Date());


  const [imagePreview, setImagePreview] = useState('');
  const [image, setImage] = useState("");
  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);


  function handlePreview(image) {
    setImagePreview(URL.createObjectURL(image));
    setImage(image);
  }

  const handleChange = (e) => {
    if (e.target.files[0]) {
      handlePreview(e.target.files[0]);
    }
  };
  console.log('preview:', imagePreview);


  function handleUpload() {
    const uploadTask = storage.ref(`projectFile/${image.name}`).put(image);
    console.log("image:", image);
    console.log("image name:", image.name);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        //progress function
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        //error function
        console.log(error);
        alert(error.message);
      },
      () => {
        //complete function
        storage
          .ref("projectFile")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            //post image inside db
            console.log("url:" + url);
            db.collection("project").add({

              category: category,

              programTitle: programTitle,

              projectTitle: projectTitle,
              projectLeader: projectLeader,
              agency: agency,
              address: address,
              telephone: telephone,
              mobile: mobile,
              fax: fax,
              email: email,

              cooperatingAgency: cooperatingAgency,
                createdAt: createdAt,






              imageUrl: url,
              timestamp: firebase.firestore.FieldValue.serverTimestamp(),

            });
          });



          setCategory("");
          setProgramTitle("");

          setProjectTitle("");
          setProjectLeader("");
          setAgency("");
          setAddress("");
          setTelephone("");
          setMobile("");
          setFax("");
          setEmail("");
          setCooperatingAgency("");
          setCreatedAt("");



        setProgress(0);
        setImage(null);
        setImagePreview('');
      }
    );
  }

  return (
    <>


      <div >
              <h1>Upload Project </h1>






        <select onChange={(e) => setCategory(e.target.value)}>
        <option name="category" id="category" value="">Select Category</option>
       <option name="category" id="category" value="CLSD project">CLSD Project</option>
       <option name="category" id="category" value="Research and Development ">Research and Development </option>
       <option name="category" id="category" value="In-house Research">In-house Research</option>
       <option name="category" id="category" value="Others"> Others</option>
       </select>
       <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter program title"
          minRows={10}
          placeholder="Enter program title "
          style={{ width: 450 }}
          value={programTitle}
          onChange={(e) => setProgramTitle(e.target.value)}
          className="input"
          />

        <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter project title"
          minRows={10}
          placeholder="Enter project title"
          style={{ width: 450 }}
          value={projectTitle}
          onChange={(e) => setProjectTitle(e.target.value)}
          className="input"
          />

         <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter project leader "
          minRows={10}
          placeholder="Enter project leader "
          style={{ width: 450 }}
          value={projectLeader}
          onChange={(e) => setProjectLeader(e.target.value)}
          className="input"
          />
             <br></br>

             <TextareaAutosize
               maxRows={2}
               aria-label="Enter agency"
               minRows={10}
               placeholder="Enter agency "
               style={{ width: 450 }}
               value={agency}
               onChange={(e) => setAgency(e.target.value)}
               className="input"
               />


     <br></br>
     <TextareaAutosize
       maxRows={2}
       aria-label="Enter Address"
       minRows={10}
       placeholder="Enter Address "
       style={{ width: 450 }}
       value={address}
       onChange={(e) => setAddress(e.target.value)}
       className="input"
       />


<br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter telephone number "
          minRows={10}
          placeholder="Enter telephone number"
          style={{ width: 450 }}
          value={telephone}
          onChange={(e) => setTelephone(e.target.value)}
          className="input"
          />

     <br></br>


     <TextareaAutosize
       maxRows={2}
       aria-label="Enter mobile number "
       minRows={10}
       placeholder="Enter mobile number"
       style={{ width: 450 }}
       value={mobile}
       onChange={(e) => setMobile(e.target.value)}
       className="input"
       />


     <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter fax "
          minRows={10}
          placeholder="Enter fax"
          style={{ width: 450 }}
          value={fax}
          onChange={(e) => setFax(e.target.value)}
          className="input"
          />

     <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter email address "
          minRows={10}
          placeholder="Enter email address "
          style={{ width: 450 }}
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="input"
          />



        <DatePicker
        selected={createdAt}
        onChange={date => setCreatedAt(date)}
        />


     <br></br>
        <input
          type="file"
          onChange={handleChange}
          className="input"
          style={{ display: "none" }}
          id="raised-button-file"
        />
        <label htmlFor="raised-button-file">
          <IconButton
            variant="raised"
            component="span"
            className="button"
            color="primary"
          >
          <img
        src={imagePreview||"https://www.brno.cz/o/brno-theme/assets/images/placeholders/avatar/avatar-unisex.png?v=0.1.0"}
        alt=''
          className="image"
        width="150"
        height="150"
      />
            <PhotoCamera />
          </IconButton>
        </label>
        <LinearProgress
          variant="determinate"
          color="primary"
          value={progress}
            className="progress"
        />
        <Button variant="outlined" onClick={handleUpload}>
          Upload
        </Button>
      </div>
    </>
  );
}
