

import React, { useEffect, useState } from "react";
import {useNavigate} from "react-router-dom"
import {
    Card,
    Col,
    Row,Container
} from "react-bootstrap";
import { Button } from "@mui/material";

import {paragraph} from "./styles.css";


export default function EventPost({id,
  imageUrl,
classification,
firstName,
middleName,
lastName,
suffix,
designation,
department,
educationI,
educationII,
educationIII,
licensure,
others,
email,
researchInterest
 }) {


  return (


<>




          <Card className='mt-4' bg="light" >
           <Container className='mt-4 mb-4' >
          <Row>

          <Col   md= '3'>

            <Card.Img  class="rounded-circle" variant="top" src={imageUrl} height="300" width="250"/>

          </Col>

               <Col md='6'>

                       <Card.Title><h2>  {classification}</h2> </Card.Title>
                       <Card.Body >
                               <h3>{firstName} {middleName} {lastName}, {suffix} </h3>
                                   {designation}  ,  {department}   <br></br>
                                 <Row className='mt-4' >
                                   <Col >
                                   <h5>Education Background </h5>

                                     {educationI}  <br></br>
                                     {educationII} <br></br>
                                     {educationIII} <br></br>


                                     {licensure}  <br></br>
                                     {others}

                                      <br></br><br></br>

                                       {email}  
                                   </Col>
                                   <Col >
                                     <h5>Research Interest </h5>
                                     {researchInterest}    <br></br>




                                   </Col>
                                 </Row>
                       </Card.Body>


                 </Col>
          </Row>
            </Container >
          </Card>





</>





  );
}
